<template>
  <div class="shoppCart">
    <div class="navtop">
      <van-nav-bar title="购物车">
        <template #right>
          <span @click="toggleEditStatus" v-if="shoppCartAllData.length > 0">{{!isEdit ? '编辑' : '完成'}}</span>
        </template>
      </van-nav-bar>
    </div>

    <!-- 购物车数据 -->
    <div class="shoppCartList" v-if="shoppCartAllData.length > 0">
      <van-swipe-cell class="goods_cell" v-for="(item,i) in shoppCartAllData" :key="i">
        <div class="goods">
          <!-- 复选框 -->
          <div class="check">
            <van-checkbox v-model="item.isChecked" icon-size="20px" @click.stop="simpleSelect"/>
          </div>

          <img :src="item.image" @click.stop="goDetails(item.id)"/>
          <div class="goodsText">
            <p class="title" @click.stop="goDetails(item.id)">{{item.name}}</p>
            <span class="productValue">属性：{{item.suk}}</span>
            <div class="priceBox">
              <span class="price">￥{{item.price}}</span>
              <van-stepper
                v-model="item.goodsNum"
                theme="round"
                disable-input
                button-size="22"
                max="5"
                @change="updateShoppCartCount(item.numId,item.goodsNum)"
              />
            </div>
          </div>
        </div>

        <!-- 滑动删除 -->
        <template #right>
          <van-button square text="删除" type="danger" class="delete-button" @click="removeShoppCartData([item.numId], i)" />
        </template>
      </van-swipe-cell>

      <van-submit-bar v-show="!isEdit" :price="amount" button-text="提交订单" button-color="#ff0034"  @submit="commit">
        <van-checkbox v-model="checked" @click="allSelect">全选</van-checkbox>
      </van-submit-bar>

      <van-submit-bar v-show="isEdit" @submit="removeSelect" button-text="删除选择" button-color="#ff0034">
        <van-checkbox v-model="checked" @click="allSelect">全选</van-checkbox>
      </van-submit-bar>
    </div>

    <!-- 空状态 -->
    <div class="empty" v-else>
      <img src="../assets/image/shoppCartempty.png" alt="">
      <p class="title">购物车竟然是空的</p>
      <p class="sub">再忙，也要记得买点什么犒劳一下自己哈~</p>
      <button class="button" @click="goMain">去逛一逛</button>
    </div>

    <!-- 猜你喜欢 -->
    <GoodsList :List="GoodsList"><div class="list_hd"><img src="https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png"></div></GoodsList>

  </div>
</template>

<script>
import "../assets/style/shoppCart.less";
import GoodsList from "../components/GoodsList"
export default {
  name: "shoppCart",
  components:{
    GoodsList
  },
  data() {
    return {
      //是否编辑商品
      isEdit: false,

      //购物车数据
      shoppCartAllData: [],

      //全选或者取消全选
      checked: false,

      // 总金额
      amount:0,

      //猜你喜欢的商品数据
      GoodsList:[]

    };
  },
  created() {
    this.getShoppData();

    // 获取猜你喜欢的数据
    this.axios(this.baseUrl+"/groom/list/2?page=1&limit=30").then(data=>{
      this.GoodsList = data.data.data.list;
    })
  },
  methods: {
    //返回上一级
    back() {
      this.$router.back();
    },

    //切换编辑状态
    toggleEditStatus() {
      this.isEdit = !this.isEdit;
    },

    //获取购物车数据
    getShoppData() {
      //获取tokekn
      let tokenString = this.$cookies.get("tokenString");

      //判断token存不存在
      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "GET",
        url: this.baseUrl + "/cart/list",
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      }).then((res) => {
          this.$toast.clear();

          if (res.data.status == 410000) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }

          //处理数据
          if (res.data.status === 200) {
            res.data.data.valid.forEach((item) => {
              this.shoppCartAllData.push({
                isChecked: false,
                id: item.productInfo.id,
                goodsNum: item.cart_num,
                price: item.productInfo.price,
                image: item.productInfo.image,
                name: item.productInfo.store_name,
                suk: item.productInfo.attrInfo.suk,
                numId:item.id
              });
            });
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //修改购物袋数量
    updateShoppCartCount(id, number) {
      let tokenString = this.$cookies.get("tokenString");

      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/cart/num",
        data: {
          id,
          number,
        },
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      }).then(() => {
          this.sum()

        }).catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
        

    },


    //全选
    allSelect() {
      //将所有单选状态修改位全选状态
      this.shoppCartAllData.forEach(item => {
        item.isChecked = this.checked;
      });

      this.sum();
    },

    //单选
    simpleSelect() {
      this.sum();

      //只要有一个单选没有勾选，则全选不能勾选
      for (const item of this.shoppCartAllData){
        if (!item.isChecked) {
          this.checked = false;
          return;
        }
      }

      this.checked = true;//如果都选了全选也要选上
    },

    //删除购物车数据, 
    removeShoppCartData(ids, index) {
      let tokenString = this.$cookies.get("tokenString");

      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/cart/del",
        data: {
          ids
        },
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((res) => {
          this.$toast.clear();

          if (res.data.status == 200) {

            //如果是单个
            if (index != undefined) {
              this.shoppCartAllData.splice(index, 1);
              
              this.simpleSelect();
            } else {
              //执行多个删除
              for (let i = 0; i < this.shoppCartAllData.length; i++) {
                if (this.shoppCartAllData[i].isChecked) {
                  this.shoppCartAllData.splice(i, 1);
                  i--;
                }
              }
              this.sum();
            }
            this.$toast({
            message: "删除成功！",
            forbidClick: true,
            duration: 3000,
          });

            
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //删除选择的购物车商品
    removeSelect() {
      //获取勾选的sid
      let ids = [];
      this.shoppCartAllData.forEach(item => {
        if (item.isChecked) {
          ids.push(item.numId);
        }
      });

      if (ids.length === 0) {
        this.$toast({
          message: "未选择删除商品",
          forbidClick: true,
          duration: 3000,
        });

        return;
      }

      console.log("sids ==> ", ids);

      this.removeShoppCartData(ids);

    },

    //计算勾选的商品总金额
    sum() {
      let amount = 0;
      this.shoppCartAllData.forEach(item => {
        if (item.isChecked) {
          amount += item.price * item.goodsNum;
        }
      });

      this.amount = amount * 100;
    },

    //去到首页
    goMain(){
      this.$router.push("/")
    },

    //去到详情页
    goDetails(id){
      this.$router.push(`/details/${id}`)
    },

    //提交订单
    commit() {
      //获取勾选的sid
      let ids = [];

      this.shoppCartAllData.forEach((v) => {
        if (v.isChecked) {
          ids.push(v.numId);
        }
      });

      if (ids.length === 0) {
        this.$toast({
          message: "选择下单的商品",
          forbidClick: true,
          duration: 3000,
        });

        return;
      }

      this.$router.push({name: 'BuyNow', params: {id: ids.join(',')}});

    },

  },
};
</script>
